//Fonts
$font-family-sans-serif:    "Source Sans Pro", Helvetica, Arial, sans-serif;
$primaryFont:               "Source Sans Pro", Helvetica, Arial, sans-serif;
$base-font-family: 			"tinos", Helvetica, Arial, sans-serif !default;
$headings-font-family:      "tinos", Helvetica, Arial, sans-serif;
$font-family-serif:         "tinos", Helvetica, Arial, sans-serif;

$weightLight: 300;
$weightRegular: 400;
$weightMedium: 500;
$weightSemibold: 600;
$weightBold: 700;
$weightUltabold: 900;


$font-size-base:            16px;
$font-unit:                 0em; 
$browser-context:           $font-size-base;
$baseFontSize:              16px; /* ($baseFontSize * 1) + 0px; */
$viewportSize: 100%;

$headings-font-weight:      700;
$font-size-h1:              ((60px / $font-size-base) + $font-unit); //48px
$font-size-h2:              ((34px / $font-size-base) + $font-unit); //26px
$font-size-h3:              ((31px / $font-size-base) + $font-unit); //22px
$font-size-h4:              ((22px / $font-size-base) + $font-unit); //20px
$font-size-h5:              ((18px / $font-size-base) + $font-unit);
$font-size-h6:              $font-size-base;

$gray-base:                 #000 !default;
$gray-darker:               lighten($gray-base, 13.5%) !default; // #222
$gray-dark:                 lighten($gray-base, 20%) !default;   // #333
$gray:                      lighten($gray-base, 33.5%) !default; // #555
$gray-light:                lighten($gray-base, 46.7%) !default; // #777
$gray-lighter:              lighten($gray-base, 93.5%) !default; // #eee

$gray-bck:                  #d8d8d8;
$gray-text:                 #6c6c6c;

//colors
$brand-primary:             #000; 
$brand-secondary:           #e22f22;
$brand-purple:	             #5271ff;
$text-color:                #000;
$light-color:      			#f7f7f7;
$dark-color:       			#212427;
$white:						#ffffff;
$black:             		#000000;
$blue:              		#46a5fc;
$lblue:						#aad9ed;
$transblack:				#00000060;
$transparent:				rgba(0,0,0,0);
$gray-2-color:     			#efefef;
$gray-3-color:     			#dfe0e1;
$gray-4-color:     			#bfc3c3;
$grey:						#808080;
$border-color:     			#e0e0e0;
$base-color:       			#4B4B4B;
$header-color:     			#4B4B4B;
$error-color:      			#e73d4a;
$success-color:    			#17aa1c;
$warning:           		#fecd50;
$selected-color:   			#212427;
$hover-color:      			#303030;
$visited-color:    			#121212;
$link-color:       			#212427;
$link-hover-color: 			#303030;



//Buttons
$btn-primary-color:              #fff !default;
$btn-primary-bg:                 $brand-primary !default;
$btn-primary-border:             darken($btn-primary-bg, 5%) !default;


// Cards
$cards: (
	article:     		#fece0f,
	facebook:    		#3d5c95,
	google-plus: 		#f37b65,
	instagram:   		#9f1e97,
	linkedin:    		#0077b5,
	twitter:     		#2eb1eb,
	youtube:     		#e34336,
    vimeo:       		#00b3ec,
	red: 				#e22f22,
	lightgrey:          #97999c,
	lightergrey: 		#E5E5E5,
	caption: 			#6c6c6c,
	grey-text: 			#4a4a4a,
	form-explainer:		#0e0e0e,
	message:             #dadada,
	user-editor:   		rgba(151, 153, 156, 0.2),

    marketgrey:         rgba(216,216,216,.3),

);




//Responsive break points
$super-large-break:         1600px;
$logo-break:                1580px;
$large-break:				1450px;
$header-break:              1120px;
$menu-break:                1260px;
$md-break:                  1200px; /* col-lg  */
$mmd-break:                 992px;
$mmd-break-down:            991px;  /* col-md ipad landscape */
$sm-break:                  768px;  /* col-sm ipad */
$mobile-break:              620px;	/* col-xs */
$sm-mobile-break:           490px;	/* col-xs */
$mmd-break-down:            991px;  /* col-md ipad landscape */
$sm-break-down:             767px;  /* col-sm ipad */

//Navbar
$logo-width:                52px;
$logo-height:               52px;
$sticked-logo-height:       34px;
$sticked-logo-width:        34px;
$navbar-height:             100px;

