.card-election{

    margin-bottom: 79.5px;
    @media #{$mobile}{
        margin-bottom: -2px;
    }


    &.inverted{
        @media screen and (min-width: $mobile-break){
            > a.card{
                background-color: #000;
                color: #FFF;
                
                > article { 
                    -ms-flex-direction: row-reverse;
                    -webkit-flex-direction: row-reverse;
                    flex-direction: row-reverse;
                }

                > article > .content{
                    .c-cards-view__comment-count {
                        color:white;
                    }

                    > .category{
                        color: #27aae1;
                    }

                    > .author p{
                        color: #FFF;
                    }

                    > time{
                        display:none;
                        color: #FFF;
                    }
                    > p {
                        display: none;
                    }
                }
            }
        }
    }


    a.card{

        @media #{$mobile}{
            min-height: 115px;
            margin-left: -15px;
            margin-right: -15px;
            border-top: 2px solid #dadada;
            border-bottom: 2px solid #dadada;
            height: auto;
            
            > article {
                display: -webkit-box;
                display: -webkit-flex;
                display: -ms-flexbox;
                display: flex;

                flex-wrap: nowrap;
                -webkit-flex-wrap: nowrap;
            }    
        }

        > article > figure{
            width: auto;
            overflow: hidden;
            height:215px;

            @media #{$mobile}{
                width: 125px;
                flex: 0 0 125px;
            }

            > img{
                max-width: 100%;
            }
        }

        > article > .content{
            display:block;
            position:relative;
            height: 139px;

            padding: 13px 0;
            @media #{$mobile}{
                padding: 10px;
                border: 0;
            }

            > .category{
                @include font-size(14);
                font-weight: bold;
                color: #ebb913;
                display: block;
                line-height: 1;
                margin-bottom: 8px;
                @media #{$mobile}{
                    @include font-size(11);
                    margin-bottom: 5px;
                }
            }

            > h2{
                margin-top:10px;
                font-family: $base-font-family;
                font-weight: normal;
                line-height: 1.14;
                color: #000000;
                @include font-size(26);
                max-height:58px;
                height:58px;

                @media #{$mobile}{
                    @include font-size(16);
                }
            }

            > .author{
                >img {
                    vertical-align:baseline;
                }
                > p {
                    white-space: normal;
                    padding:0;
                    height:26px;
                    overflow:hidden;
                }
                // display: none;
            }

            > time{
                position:absolute;
                top:15px;
                right:0;
                float:none;
                padding:0;
                border:none;
                color: #8a8b93;

                @media #{$mobile}{
                    color: #6c6c6c;
                    @include font-size(10);
                }
            }

            > p {
                display: none;
            }
        }




        &.youtube{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }
        &.facebook{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }
        &.pinterest{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }
        &.vimeo{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }

        &.twitter{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }

        &.instagram{
            .content{
                p{
                    display:block;
                    font-size: 28px;
                    font-weight: normal;
                }
            }
        }

    }
}