.article-gallery {

}

.article-gallery__image {
    position:relative;
    @media #{$mobile} {
        height:50vw;
        margin-bottom: 25px;
    }
    
    @media #{$tablet} {
        height:390px;
    }
    @media #{$desktop-up} {
        height:515px;
        // .external-article-video{
        //     height: 641px !important;
        // }
    }
}


.article-gallery__img-container-article {
    height:100%;
}


.article-gallery__img {
    width:100%;
    height:100%;
    object-fit: cover;
}


.article-gallery__img-container {
    @media #{$tablet}{
        height: 400px;
    }
}


.article-gallery__caption {

    margin-top: 10px;

    @media screen and (max-width: $sm-break-down) {
        float: left;
        margin-bottom: 30px;
    }
    
    > p {
        @include font-size(15);
        line-height: 18px;
        color: #4C4C4C;   
        font-style: italic;
        font-family: $base-font-family;
        float: left;

        &.credit{
            font-style: normal;
            float: right;

            @media screen and (max-width: $sm-break-down) {
                float: left;
            }
        }
    }
}

.article-gallery__single {
    >.article-gallery__image-article{
        >.external-article-video {
            max-width: 915px;
        }
        >.article-video{
            height: 100%;
        }
    }

}

.tessstt_data_cont {
    overflow: hidden;
}