.sidebar-news{
    margin-bottom: 70px;
    
    @media screen and (max-width: $mmd-break-down) {
        display:none;
    }
    @media #{$desktop-up} {
        margin-top: 40px;
    }



    a.card{
        margin-bottom: 10px;
        width: 100%;
    
        > article {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            flex-wrap: nowrap;
            -webkit-flex-wrap: nowrap;
        }

        > article > figure{
            display:none;
            // width: 100px;
            // height:100px;
            // flex: 0 0 100px;
            // overflow: hidden;
            // float: left;
            // margin-right: 19px;

            // > img{
            //     max-width: 100%;
            // }
        }

        > article .content{
            border-bottom: 1px solid #dadada;
            padding-bottom: 10px;
            width: 100%;
            -ms-flex: 1 0;
            -webkit-flex: 1 0;
            flex: 1 0;
            
            > .category{
                display:none;
            }

            > h2{
                font-family: $base-font-family;
                font-weight: 700;
                @include font-size(16);
                //height: 20px;
                overflow: hidden;
                margin-bottom: 5px;
                text-overflow: ellipsis;
                line-height: 1.2;
                @media screen and (max-width: $sm-break-down) {
                    @include font-size(20);
                }



            }

            > p {
                display:none;
            }

            > .author{
                display: none;
            }

            > time{
                padding: 0;
                border: 0;
                line-height: 1;
                float: left;
                color: #f73d30;
                @include font-size(12);
            }
        }

    }
}